@import "../../../styles/vars";
@import "../../../styles/mixin";

.footer {
  bottom: 0;
  width: 100%;
  align-items: flex-start;
  background-color: map-get($colors , intenseBlack );
  color: map-get($colors, white);
  display: flex;
  max-width: 100%;
  flex-direction: row;
  justify-content: center;
  column-gap: 30%;
  padding-top: 2rem;
  height: 30vh;
  &__contact {
    display: flex;
    flex-direction: column;
    &-list {
      display: flex;
      flex-direction: column;
      padding-top: 0.8rem;
      a {
        color:white;
        transition: 0.5s;
        &:hover {
          color: map-get($colors , orangeShade1 );
          transition: 0.5s;
        }
      }
    }
  }
  &__review {
    display: flex;
    flex-direction: column;
    &-list {
      display: flex;
      flex-direction: column;
      padding-top: 0.8rem;
      a {
        color:white;
        transition: 0.5s;
        &:hover {
          color: map-get($colors , orangeShade1 );
          transition: 0.5s;
        }
      }
    }
  }
  &__social {
    display: flex;
    flex-direction: column;
    column-gap: 10%;
    &-list {
      padding-top: 0.8rem;
      text-decoration: none;
      color:white;
      a {
        color:white;
        transition: 0.5s;
        &:hover {
          color: map-get($colors , orangeShade1 );
          transition: 0.5s;
        }
      }
    }
  }
  &__button {
    background-color: transparent;
    color:map-get($colors , white );
    border: none;
    text-decoration: underline;
    cursor: pointer;
    transition: 0.5s;
    padding-bottom: 0.5rem;
    &:hover {
      color:map-get($colors , orangeShade1 );
      transition: 0.5s;
    }
  }
}
