@import "../../../styles/vars";
@import "../../../styles/mixin";

.user-profile-modal {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    right:0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index:11;
    padding-top: 3rem;
    &__container {
        background-color: map-get($colors, black);
        color: map-get($colors, white);
        border-radius: 5rem;
        display: flex;
        flex-direction: column;
        width: 70rem;
        // height: 48.5rem;
        height: 91vh;
    }
    &__text {
        color: map-get($colors, orangeShade1);
    }
    &__button {
        float: right;
        background-color: transparent;
        color: map-get($colors, white);
        border: none;
        cursor: pointer;
    }
    &__image {
        width: 5rem;
        height: 5rem;
    }
    &__user {
        display: flex;
        flex-direction: row;
        justify-content: center;
        column-gap: 5%;
    }
    &__details {
        
    }
    &__x {
        margin: 0.10rem 4rem 0rem 0rem;
        float: right;
        background-color: transparent;
        color: map-get($colors, white);
        cursor: pointer;
        border: none;
    }
}