@import "../../../styles/vars.scss";

.trending {
    padding: 5rem 0rem 5rem 0rem;
    border-radius: 7rem;
    margin: 2rem 5rem 2rem 5rem;
    background-color: map-get($colors, black);
    &__container {
        // display: flex;
        // justify-content: center;
        // align-items: center;
    }
    &__list {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__title {
        color: map-get($colors, white);
        text-align: center;
        padding-bottom: 2rem;
    }
    &__count {
        color: map-get($colors, white);
        padding-right: 1rem;
    }
}